.div-p{
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    text-align: justify;
}
.img-t{
    margin-left: 18px;
    height: 18em;
    width: 14em;
}
.img-s{
    height: 18em;
    width: 14em;
}
.head-1{
    text-align: left;
}
.div-1{
    background-color: beige;
    width: 50%;
}
.img-s:hover,.img-t:hover{
    transform: scale(1.3);
    z-index: 2;
}
.contentbox{
    width: 50%;
}
@media(max-width:400px)
{
    .contentbox
    {
        width: 100%;
    }
    .img-s,.img-t{
        width: 90%;
    }
    .contentbox{
        height: 36em;
    }
    .img-s{
        margin-top: -3em;
        height: 16em;
    }
    .img-t{
        margin-top:1em;
        height: 18em;
    }
}