.infra{
    background-image: url('../Images/image4.jpeg');
    background-size: cover; 
    height:14em;
    color: white;
    filter:brightness(0.5);
}
.contentinfra{
    width: 100%;
    list-style-position: inside;
    text-align: justify;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.line{
    width: 75%;
    border: dotted;
    margin-left: 12%;
}
h2{
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}
.div-a,.div-b,.div-c{
    width: 75%;
    margin-left: 12%;
}
.img-1{
    margin-left: 0.6%;
    margin-bottom: 0.6%;
    border-radius: 8px;
    height: 9em;
    width: 16em;
}
.img-1:hover{
    transform: scale(1.3);
    z-index: 2;
}
@media(max-width:400px)
{
    button{
        width: 14em;
    }
    .div-a,.div-b,.div-c{
        width: 100%;
        margin-left: 0;
    }
    .line{
        width: 100%;
        margin-left: 0;
    }
    .img-1{
        width: 100%;
        margin-top: 2%;
    }
}