.contact{
    background-image: url('../Images/image4.jpeg');
    background-size: cover; 
    height:14em;
    color: white;
    filter:brightness(0.5);
}
.contact-form {
    width: 60%;
    margin: 0 auto;
    padding: 30px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
  }
  .form{
    margin-top: 2%;
  }
  .colour{
    background-color: aliceblue;
    font-size: larger;
    font-weight:bold;
  }
  #mess{
    width: 90%;
  }
  #button{
    margin-left: 18%;
  }
  .contact-form .form-group {
    float: left;
    margin-bottom: 20px;
    width: 45%;
    margin-left: 4%;
  }
  
  .contact-form label {
    font-weight: bold;
    display: block;
    margin-bottom: 5px;
  }
  
  .contact-form input,
  .contact-form textarea,
  .contact-form select {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  .contact-form textarea {
    height: 150px;
  }
  
  .contact-form button {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s;
}
.team{
    width: 75%;
    margin-top: 1%;
    margin-left: 12.5%;
}
.h2{
  font-family: 'Times New Roman', Times, serif;
}
.card{
  background-color: #ddd;
}
.img-r{
  height: 75%;
}
.map2{
  margin-top: 1%;
  background-image: url('../Images/pexels-mcraftpix-21014.jpg');
  background-size: cover; 
  height:28em;
}
@media(max-width:400px)
{
  .contact-form{
    width: 100%;
  }
  .contact-form .form-group{
    float: none;
    width: 100%;
  }
  .h2-head{
    font-weight: bold;
    font-size:x-large;
  }
  #button{
    margin-left: 9%;
    width: 80%;
  }
  .map2{
    height: 16em;
  }
}