.about{
    background-image: url('../Images/image4.jpeg');
    background-size: cover; 
    height:14em;
    color: white;
    filter:brightness(0.5);
}
button{
    border-radius: 8px;
    height: 20%;
    width: 15%;
    filter:brightness(1.8);
}
.data{
    margin-top: 2%;
    margin-left: 10%;
    margin-right: 10%;
    margin-bottom: 2%;
    text-align: justify;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.dataul{
    margin-left: 12.5%;
    width: 75%;
    list-style-position: inside;
    text-align: justify;
    height: 22em;
    background-color:darkgray;
    display: flex;
}
.uldata{
    float: left;
    font-size: larger;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.ul1,.ul2,.ul3{
    width: 28%;
    margin-left: 4%;
}
.data2{
    background-color:darkgray
}
.data1,.data2,.data3{
    text-align: justify;
    width: 100%;
}
.nestdata1,.nestdata2,.nestdata3{
    width: 75%;
    margin-left: 12%;
}
p{
    font-family: 'Courier New', Courier, monospace;
}
@media(max-width:400px)
{
    button{
        width: 10em;
    }
    .dataul{
        width: 100%;
        display:block;
        height: 55em;
        margin-left: 0;
    }
    .ul1,.ul2,.ul3{
        margin-left: 0;
        width: 100%;
    }
    .ul3{
        margin-top: 10em;
    }
    .data{
        width: 100%;
        margin-left: 0;
    }
}