.div-1{
    list-style-type: none;
    height: 18em;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.fotter{
    height: 23em;
    width: 100%;
    background-color:black;
    color: white;
}
.ul-5{
    margin-left: 14em;
}
.ul-6
{
    margin-left: 6em;
}
.dotted-line{
    width: 100%;
    height: 80%;
    border: 1px double white;
}
@media(max-width:400px)
{
    .fotter{
        margin-top: 0.2em;
        height: 28em;
    }
    .div-2,.div-3{
        height: 12em;
    }
    .div-4{
        margin-top: 0;
    }
    .ul-5{
        margin-left: 0;
    }
    .ul-6{
        margin-left: 0;
    }
}