.product{
    margin-top: 2%;
    height:55em;
}
.img{
    height: 100%;
}
.img-box{
    float: left;
    margin-left: 4%;
    margin-top: 4%;
    height: 40%;
    width: 28%;
}
.img:hover{
    transform: scale(1.3);
    z-index: 2;
}
@media(max-width:400px)
{
    .img-box{
        float: none;
        margin-left: 0;
        width: 100%;
    }
}