.logonav{
    margin-left: 2%;
}
.homenav{
    margin-left: 18%;
    font-size: larger;
    cursor: pointer;
}
.product{
    margin-left: 12.5%;
    width: 75%;
}
.content{
    margin-top: 4%;
    width: 80%;
    margin-left: 12.5%;
    display:flex;
}
.chooseus{
    margin-top: 2%;
    background-image: url('../Images/image8.jpeg');
    background-size: cover; 
    height:22em;
    color: white;
    filter:brightness(0.5);
}
.infra{
    margin-top: 2%;
    background-image: url('../Images/image15');
    background-size: cover; 
    height:38em;
    filter:brightness(0.6);
}
.map{
    margin-top: 0.1%;
    background-image: url('../Images/pexels-mcraftpix-21014.jpg');
    background-size: cover; 
    height:28em;
}
.lang{
    margin-left: 2em;
}
.list{
    list-style-position:inside;
    text-align: justify;
    margin-left: 35%;
    font-family:monospace;
}
#nav{
    height: 7em;
}
#logo{
    height: 5em;
}
@media(max-width:400px)
{
    .btn{
        height: 3em;
        width: 4em;
    }
    #nav{
        height: 6em;
    }
    #logo{
        height: 4em;
        border-radius: 20px;
    }
    .content{
        margin-left: 0px;
        width: 100%;
        display:block;
    }
    .chooseus{
        height: 30em;
    }
    .list{
        margin-left: 0px;
        text-align: justify;
    }
    .product{
        width:100%;
        margin-left: 0;
        overflow: scroll;
    }
    .infra{
        height: 50em;
        margin-top: 1em;
    }
    .map{
        margin-top: 33.5em;
        height:20em;
    }
}